.top-navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: fixed;
  padding: 5px;
  background: rgb(202, 202, 202);
  color: black;
  height: 64px;
  width: 100%;
}

a.aero-font {
  font-size: 1.5rem;
  margin: 0.5rem;
}

.nav-link {
  margin: 0.5rem;
  color: #111;
  font-family: Montserrat, sans-serif;
}

.nav-link:hover {
  text-decoration: underline;
}

.aero-font:hover {
  -webkit-text-decoration-color: #e8064d;
  text-decoration-color: #e8064d;
}

.right-icons {
  margin-left: auto;
  margin-right: 5px;
}

.icon-link {
  font-size: 1.5rem;
}

@media (prefers-color-scheme: dark) {
  .top-navbar {
    background-color: black;
    color: white;
  }

  .nav-link {
    color: #ddd;
  }
}

.commands {
  background-color: #111;
  color: white;
  padding-top: 0.5rem;
}

.commands-sidebar {
  padding-left: 2rem;
  font-family: Montserrat;
  padding-right: 6rem;
  border-right: 3px solid #e8064d;
}

.commands-sidebar .aside-link {
  padding-bottom: 0.25rem;
}

.commands article {
  padding-left: 1rem;
  width: 75%;
}

.commands h3 {
  font-size: 2rem;
}

.commands article hr {
  margin-top: 0.5rem;
  border: 1px solid #a5a5a5;
  max-width: 50%;
}

.commands .pre-commands {
  font-size: 1.5rem;
  font-family: Montserrat;
}

.commands-main code {
  color: #fafa1bc9;
}

.command-info {
  padding-bottom: 0.5rem;
  max-width: 75%;
  border-bottom: 1px solid #ccc;
}

.command-info:last-child {
  border-bottom: none;
}

.full-screen {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

